/**
 * 
 * 
 * React Hook FormYupTypeScript
 * 
 * 
 * @author fenix
 * @date 2025-01-27
 * @version 1.0
 */

import React, { useState, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { Link, useNavigate } from 'react-router-dom';
import { EyeIcon, EyeSlashIcon } from '@heroicons/react/24/outline';
import { object, boolean, string, ref } from 'yup';
import { RegisterRequest } from '../../types/auth';
import { useAuthStore } from '../../store/authStore';
import { AUTH_ROUTES } from '../../constants/auth';
import { validateUsername, validateEmail, calculatePasswordStrength } from '../../utils/validation';

/**
 * 
 * 
 * TypeScript
 * IDE
 */
interface RegisterFormData {
  /**  */
  username: string;
  /**  */
  email: string;
  /**  */
  password: string;
  /**  */
  confirmPassword: string;
  /**  */
  nickname: string;
  /**  */
  mobile: string;
  /**  */
  agreeTerms: boolean;
}

/**
 * 
 * 
 * TypeScript
 * 
 */
interface RegisterFormProps {
  /**  */
  redirectTo?: string;
  /**  */
  showLoginLink?: boolean;
  /**  */
  className?: string;
  /**  */
  onRegisterSuccess?: () => void;
}

/**
 * 
 * 
 * React
 * 
 */
interface PasswordStrengthProps {
  password: string;
}

const PasswordStrength: React.FC<PasswordStrengthProps> = ({ password }) => {
  const strength = calculatePasswordStrength(password);
  
  const getStrengthColor = (level: string) => {
    switch (level) {
      case 'weak': return 'bg-red-500';
      case 'medium': return 'bg-yellow-500';
      case 'strong': return 'bg-green-500';
      case 'very_strong': return 'bg-green-600';
      default: return 'bg-gray-300';
    }
  };
  
  const getStrengthText = (level: string) => {
    switch (level) {
      case 'weak': return '';
      case 'medium': return '';
      case 'strong': return '';
      case 'very_strong': return '';
      default: return '';
    }
  };
  
  if (!password) return null;
  
  return (
    <div className="mt-2">
      <div className="flex items-center space-x-2">
        <div className="flex-1 bg-gray-200 rounded-full h-2">
          <div 
            className={`h-2 rounded-full transition-all duration-300 ${getStrengthColor(strength.level)}`}
            style={{ width: `${(strength.score / 100) * 100}%` }}
          />
        </div>
        <span className="text-sm text-gray-600">
          : {getStrengthText(strength.level)}
        </span>
      </div>
      {strength.feedback.length > 0 && (
        <ul className="mt-1 text-xs text-gray-500">
          {strength.feedback.map((feedback, index) => (
            <li key={index}> {feedback}</li>
          ))}
        </ul>
      )}
    </div>
  );
};

/**
 * 
 * 
 * ReactHook Form
 * UI
 * 
 * @param props 
 * @returns JSX
 */
export const RegisterForm: React.FC<RegisterFormProps> = ({
  redirectTo,
  showLoginLink = true,
  className = '',
  onRegisterSuccess
}) => {
  // hooks
  const navigate = useNavigate();
  
  // store
  const { register: registerUser, isLoading, error, clearError } = useAuthStore();
  
  // 
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [usernameAvailable, setUsernameAvailable] = useState<boolean | null>(null);
  const [emailAvailable, setEmailAvailable] = useState<boolean | null>(null);
  const [isCheckingUsername, setIsCheckingUsername] = useState(false);
  const [isCheckingEmail, setIsCheckingEmail] = useState(false);
  
  // 
  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    watch,
    setFocus,
  } = useForm<RegisterFormData>({
    resolver: yupResolver(object({
      username: string().required('').min(3, '3').max(20, '20'),
      email: string().required('').email(''),
      password: string().required('').min(8, '8'),
      confirmPassword: string().required('').oneOf([ref('password')], ''),
      nickname: string().required('').min(2, '2').max(20, '20'),
      mobile: string().required('').matches(/^1[3-9]\d{9}$/, ''),
      agreeTerms: boolean().required('').oneOf([true], '')
    })),
    defaultValues: {
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      nickname: '',
      mobile: '',
      agreeTerms: false as boolean
    },
    mode: 'onChange'
  });
  
  // 
  const watchedUsername = watch('username');
  const watchedEmail = watch('email');
  const watchedPassword = watch('password');
  
  // 
  useEffect(() => {
    setFocus('username');
  }, [setFocus]);
  
  // 
  useEffect(() => {
    return () => {
      clearError();
    };
  }, [clearError]);
  
  // 
  useEffect(() => {
    const checkUsername = async () => {
      if (watchedUsername && watchedUsername.length >= 3) {
        setIsCheckingUsername(true);
        try {
          const result = await validateUsername(watchedUsername);
          setUsernameAvailable(result.isValid);
        } catch (error) {
          setUsernameAvailable(null);
        } finally {
          setIsCheckingUsername(false);
        }
      } else {
        setUsernameAvailable(null);
      }
    };
    
    const timeoutId = setTimeout(checkUsername, 500);
    return () => clearTimeout(timeoutId);
  }, [watchedUsername]);
  
  // 
  useEffect(() => {
    const checkEmail = async () => {
      if (watchedEmail && watchedEmail.includes('@')) {
        setIsCheckingEmail(true);
        try {
          const result = await validateEmail(watchedEmail);
          setEmailAvailable(result.isValid);
        } catch (error) {
          setEmailAvailable(null);
        } finally {
          setIsCheckingEmail(false);
        }
      } else {
        setEmailAvailable(null);
      }
    };
    
    const timeoutId = setTimeout(checkEmail, 500);
    return () => clearTimeout(timeoutId);
  }, [watchedEmail]);
  
  /**
   * 
   * API
   * 
   * 
   * 
   * @param data 
   */
  const onSubmit = async (data: RegisterFormData) => {
    try {
      // 
      clearError();
      
      // 
      const registerRequest: RegisterRequest = {
        username: data.username.trim(),
        email: data.email.trim().toLowerCase(),
        password: data.password,
        confirmPassword: data.confirmPassword,
        nickname: data.nickname.trim(),
        mobile: data.mobile.trim(),
        agreeTerms: data.agreeTerms
      };
      
      // API
      await registerUser(registerRequest);
      
      // 
      if (onRegisterSuccess) {
        onRegisterSuccess();
      }
      
      // 
      const targetPath = redirectTo || AUTH_ROUTES.LOGIN;
      navigate(targetPath, { 
        replace: true,
        state: { 
          message: '',
          username: data.username 
        }
      });
      
    } catch (error) {
      // store
      console.error('Registration failed:', error);
    }
  };
  
  /**
   * 
   * /
   * React
   * 
   */
  const togglePasswordVisibility = () => {
    setShowPassword(prev => !prev);
  };
  
  const toggleConfirmPasswordVisibility = () => {
    setShowConfirmPassword(prev => !prev);
  };
  
  /**
   * 
   * 
   * 
   * 
   */
  const handleInputChange = () => {
    if (error) {
      clearError();
    }
  };
  
  return (
    <div className={`w-full max-w-md mx-auto ${className}`}>
      {/*  */}
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-2">
          
        </h2>
        <p className="text-gray-600">
          
        </p>
      </div>
      
      {/*  */}
      {error && (
        <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm text-red-800">{error}</p>
            </div>
          </div>
        </div>
      )}
      
      {/*  */}
      <form onSubmit={handleSubmit(onSubmit as any)} className="space-y-6">
        {/*  */}
        <div>
          <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <div className="relative">
            <input
              {...register('username')}
              type="text"
              id="username"
              autoComplete="username"
              placeholder=""
              onChange={handleInputChange}
              className={`w-full px-3 py-2 pr-10 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
                errors.username ? 'border-red-300' : 
                usernameAvailable === false ? 'border-red-300' :
                usernameAvailable === true ? 'border-green-300' : 'border-gray-300'
              }`}
            />
            <div className="absolute inset-y-0 right-0 pr-3 flex items-center">
              {isCheckingUsername ? (
                <svg className="animate-spin h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
              ) : usernameAvailable === true ? (
                <svg className="h-5 w-5 text-green-500" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414L8.414 15l-4.121-4.121a1 1 0 011.414-1.414L8.414 12.172l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
              ) : usernameAvailable === false ? (
                <svg className="h-5 w-5 text-red-500" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              ) : null}
            </div>
          </div>
          {errors.username && (
            <p className="mt-1 text-sm text-red-600">{errors.username.message}</p>
          )}
          {!errors.username && usernameAvailable === false && (
            <p className="mt-1 text-sm text-red-600"></p>
          )}
          {!errors.username && usernameAvailable === true && (
            <p className="mt-1 text-sm text-green-600"></p>
          )}
        </div>
        
        {/*  */}
        <div>
          <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <div className="relative">
            <input
              {...register('email')}
              type="email"
              id="email"
              autoComplete="email"
              placeholder=""
              onChange={handleInputChange}
              className={`w-full px-3 py-2 pr-10 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
                errors.email ? 'border-red-300' : 
                emailAvailable === false ? 'border-red-300' :
                emailAvailable === true ? 'border-green-300' : 'border-gray-300'
              }`}
            />
            <div className="absolute inset-y-0 right-0 pr-3 flex items-center">
              {isCheckingEmail ? (
                <svg className="animate-spin h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                  <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                  <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
              ) : emailAvailable === true ? (
                <svg className="h-5 w-5 text-green-500" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414L8.414 15l-4.121-4.121a1 1 0 011.414-1.414L8.414 12.172l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                </svg>
              ) : emailAvailable === false ? (
                <svg className="h-5 w-5 text-red-500" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
                </svg>
              ) : null}
            </div>
          </div>
          {errors.email && (
            <p className="mt-1 text-sm text-red-600">{errors.email.message}</p>
          )}
          {!errors.email && emailAvailable === false && (
            <p className="mt-1 text-sm text-red-600"></p>
          )}
          {!errors.email && emailAvailable === true && (
            <p className="mt-1 text-sm text-green-600"></p>
          )}
        </div>
        
        {/*  */}
        <div>
          <label htmlFor="nickname" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <input
            {...register('nickname')}
            type="text"
            id="nickname"
            autoComplete="nickname"
            placeholder=""
            onChange={handleInputChange}
            className={`w-full px-3 py-2 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
              errors.nickname ? 'border-red-300' : 'border-gray-300'
            }`}
          />
          {errors.nickname && (
            <p className="mt-1 text-sm text-red-600">{errors.nickname.message}</p>
          )}
        </div>
        
        {/*  */}
        <div>
          <label htmlFor="mobile" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <input
            {...register('mobile')}
            type="tel"
            id="mobile"
            autoComplete="tel"
            placeholder=""
            onChange={handleInputChange}
            className={`w-full px-3 py-2 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
              errors.mobile ? 'border-red-300' : 'border-gray-300'
            }`}
          />
          {errors.mobile && (
            <p className="mt-1 text-sm text-red-600">{errors.mobile.message}</p>
          )}
        </div>
        
        {/*  */}
        <div>
          <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <div className="relative">
            <input
              {...register('password')}
              type={showPassword ? 'text' : 'password'}
              id="password"
              autoComplete="new-password"
              placeholder=""
              onChange={handleInputChange}
              className={`w-full px-3 py-2 pr-10 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
                errors.password ? 'border-red-300' : 'border-gray-300'
              }`}
            />
            <button
              type="button"
              onClick={togglePasswordVisibility}
              className="absolute inset-y-0 right-0 pr-3 flex items-center"
            >
              {showPassword ? (
                <EyeSlashIcon className="h-5 w-5 text-gray-400 hover:text-gray-600" />
              ) : (
                <EyeIcon className="h-5 w-5 text-gray-400 hover:text-gray-600" />
              )}
            </button>
          </div>
          {errors.password && (
            <p className="mt-1 text-sm text-red-600">{errors.password.message}</p>
          )}
          <PasswordStrength password={watchedPassword} />
        </div>
        
        {/*  */}
        <div>
          <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700 mb-2">
            
          </label>
          <div className="relative">
            <input
              {...register('confirmPassword')}
              type={showConfirmPassword ? 'text' : 'password'}
              id="confirmPassword"
              autoComplete="new-password"
              placeholder=""
              onChange={handleInputChange}
              className={`w-full px-3 py-2 pr-10 border rounded-lg shadow-sm placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 ${
                errors.confirmPassword ? 'border-red-300' : 'border-gray-300'
              }`}
            />
            <button
              type="button"
              onClick={toggleConfirmPasswordVisibility}
              className="absolute inset-y-0 right-0 pr-3 flex items-center"
            >
              {showConfirmPassword ? (
                <EyeSlashIcon className="h-5 w-5 text-gray-400 hover:text-gray-600" />
              ) : (
                <EyeIcon className="h-5 w-5 text-gray-400 hover:text-gray-600" />
              )}
            </button>
          </div>
          {errors.confirmPassword && (
            <p className="mt-1 text-sm text-red-600">{errors.confirmPassword.message}</p>
          )}
        </div>
        
        {/*  */}
        <div>
          <div className="flex items-start">
            <input
              {...register('agreeTerms')}
              id="agreeTerms"
              type="checkbox"
              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded mt-1"
            />
            <label htmlFor="agreeTerms" className="ml-2 block text-sm text-gray-700">
              {' '}
              <Link to="/terms" className="text-blue-600 hover:text-blue-500 hover:underline">
                
              </Link>
              {' '}{' '}
              <Link to="/privacy" className="text-blue-600 hover:text-blue-500 hover:underline">
                
              </Link>
            </label>
          </div>
          {errors.agreeTerms && (
            <p className="mt-1 text-sm text-red-600">{errors.agreeTerms.message}</p>
          )}
        </div>
        
        {/*  */}
        <button
          type="submit"
          disabled={isSubmitting || isLoading || usernameAvailable === false || emailAvailable === false}
          className="w-full flex justify-center py-2 px-4 border border-transparent rounded-lg shadow-sm text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors duration-200"
        >
          {isSubmitting || isLoading ? (
            <>
              <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              ...
            </>
          ) : (
            ''
          )}
        </button>
        
        {/*  */}
        {showLoginLink && (
          <div className="text-center">
            <p className="text-sm text-gray-600">
              {' '}
              <Link
                to={AUTH_ROUTES.LOGIN}
                className="text-blue-600 hover:text-blue-500 hover:underline font-medium"
              >
                
              </Link>
            </p>
          </div>
        )}
      </form>
    </div>
  );
};

export default RegisterForm;