import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse, AxiosError } from 'axios';
import { 
  SESSION_CONFIG, 
  AUTH_ENDPOINTS, 
  HTTP_STATUS, 
  ERROR_MESSAGES 
} from '../constants/auth';
import { 
  getToken, 
  removeToken, 
  refreshTokenIfNeeded,
  getRefreshToken,
  setToken,
  setRefreshToken,
  removeRefreshToken
} from './tokenStorage';
import type { AuthResponse } from '../types/auth';
import { 
  shouldRefreshToken,
  parseJwtPayload 
} from './auth';

/**
 * HTTP
 * API
 * TypeScript
 * 
 */
export interface ApiResponse<T = any> {
  /**  */
  code: number;
  /**  */
  message: string;
  /**  */
  data: T;
  /**  */
  timestamp: number;
  /** ID */
  traceId?: string;
}

/**
 * HTTP
 * API
 * TypeScript
 * 
 */
export interface ApiError {
  /**  */
  code: number;
  /**  */
  message: string;
  /**  */
  details?: any;
  /**  */
  timestamp: number;
  /**  */
  path?: string;
}

/**
 * 
 * Axios
 * TypeScript
 * 
 */
export interface RequestConfig extends AxiosRequestConfig {
  /**  */
  skipAuth?: boolean;
  /**  */
  skipErrorHandling?: boolean;
  /**  */
  showLoading?: boolean;
  /**  */
  retryCount?: number;
  /**  */
  retryDelay?: number;
}

/**
 * Axios
 * HTTP
 * Axios
 * 
 */
const createHttpClient = (): AxiosInstance => {
  const instance = axios.create({
    baseURL: (import.meta as any).env?.VITE_API_BASE_URL || 'http://localhost:8090',
    timeout: SESSION_CONFIG.SESSION_TIMEOUT,
    headers: {
      'Content-Type': 'application/json',
      'Accept': 'application/json',
    },
  });

  return instance;
};

/**
 * HTTP
 * HTTP
 * Axios
 * HTTP
 */
export const httpClient = createHttpClient();

/**
 * 
 * ID
 * AxiosPromise
 * 
 */
httpClient.interceptors.request.use(
  async (config: any) => {
    // ID
    const requestId = `req_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
    config.headers['X-Request-ID'] = requestId;
    
    // 
    config.headers['X-Timestamp'] = Date.now().toString();
    
    // 
    if (!config.skipAuth) {
      const token = getToken();
      
      if (token) {
        // token
        const payload = parseJwtPayload(token);
        const tokenExpiry = payload?.exp ? payload.exp * 1000 : null;
        if (shouldRefreshToken(tokenExpiry)) {
          try {
            // 
            const needRefresh = await refreshTokenIfNeeded(tokenExpiry);
            if (needRefresh) {
              const refreshToken = getRefreshToken();
              if (refreshToken) {
                // post skipAuth 
                const refreshResp = await post<AuthResponse>(
                  AUTH_ENDPOINTS.REFRESH_TOKEN,
                  { refreshToken },
                  { skipAuth: true }
                );
                const auth = refreshResp.data;
                if (auth?.accessToken) {
                  setToken(auth.accessToken);
                  if (auth.refreshToken) {
                    setRefreshToken(auth.refreshToken);
                  }
                  config.headers.Authorization = `Bearer ${auth.accessToken}`;
                } else {
                  config.headers.Authorization = `Bearer ${token}`;
                }
              } else {
                // token
                config.headers.Authorization = `Bearer ${token}`;
              }
            } else {
              // token
              config.headers.Authorization = `Bearer ${token}`;
            }
          } catch (error) {
            console.warn('Token refresh failed:', error);
            // 
            removeToken();
            removeRefreshToken();
            config.headers.Authorization = undefined;
            if (typeof window !== 'undefined') {
              window.location.href = '/login';
            }
          }
        } else {
          config.headers.Authorization = `Bearer ${token}`;
        }
      }
    }
    
    // 
    if (process.env.NODE_ENV === 'development') {
      console.log(`[HTTP Request] ${config.method?.toUpperCase()} ${config.url}`, {
        requestId,
        headers: config.headers,
        data: config.data
      });
    }
    
    return config;
  },
  (error: AxiosError) => {
    console.error('[HTTP Request Error]', error);
    return Promise.reject(error);
  }
);

/**
 * 
 * 
 * Axios
 * 
 */
httpClient.interceptors.response.use(
  (response: AxiosResponse) => {
    const requestId = response.config.headers['X-Request-ID'];
    
    // 
    if (process.env.NODE_ENV === 'development') {
      console.log(`[HTTP Response] ${response.status} ${response.config.url}`, {
        requestId,
        data: response.data
      });
    }
    
    // 
    if (response.data && typeof response.data === 'object') {
      const apiResponse = response.data as ApiResponse;
      
      // 
        if (apiResponse.code && apiResponse.code !== HTTP_STATUS.OK) {
        const error: ApiError = {
          code: apiResponse.code,
          message: apiResponse.message || '',
          timestamp: Date.now(),
          path: response.config.url
        };
        
        return Promise.reject(error);
      }
    }
    
    return response;
  },
  async (error: AxiosError) => {
    const requestId = error.config?.headers?.['X-Request-ID'];
    
    // 
    console.error(`[HTTP Response Error] ${error.response?.status || 'Network'} ${error.config?.url}`, {
      requestId,
      error: error.message,
      response: error.response?.data
    });
    
    // 
    if (error.response) {
      const status = error.response.status;
      const responseData = error.response.data as any;
      
      // 
      const apiError: ApiError = {
        code: responseData?.code || status,
        message: responseData?.message || getErrorMessage(status),
        details: responseData?.details,
        timestamp: Date.now(),
        path: error.config?.url
      };
      
      // 
      if (status === HTTP_STATUS.UNAUTHORIZED) {
        // token
        removeToken();
        
        // 
        if (!error.config?.url?.includes(AUTH_ENDPOINTS.LOGIN)) {
          window.location.href = '/login';
        }
      }
      
      // 
      if (status === HTTP_STATUS.FORBIDDEN) {
        // 
        console.warn('Access forbidden:', apiError.message);
      }
      
      // 
      if (status >= HTTP_STATUS.INTERNAL_SERVER_ERROR) {
        // 
        console.error('Server error:', apiError.message);
      }
      
      return Promise.reject(apiError);
    }
    
    // 
    if (error.request) {
      const networkError: ApiError = {
        code: 0,
        message: ERROR_MESSAGES.NETWORK_ERROR,
        timestamp: Date.now(),
        path: error.config?.url
      };
      
      return Promise.reject(networkError);
    }
    
    // 
    const unknownError: ApiError = {
      code: -1,
      message: error.message || ERROR_MESSAGES.UNKNOWN_ERROR,
      timestamp: Date.now()
    };
    
    return Promise.reject(unknownError);
  }
);

/**
 * HTTP
 * HTTP
 * 
 * 
 * 
 * @param status HTTP
 * @returns 
 */
const getErrorMessage = (status: number): string => {
  switch (status) {
    case HTTP_STATUS.BAD_REQUEST:
      return '';
    case HTTP_STATUS.UNAUTHORIZED:
      return ERROR_MESSAGES.AUTH_FAILED;
    case HTTP_STATUS.FORBIDDEN:
      return ERROR_MESSAGES.INSUFFICIENT_PERMISSIONS;
    case HTTP_STATUS.NOT_FOUND:
      return '';
    case HTTP_STATUS.CONFLICT:
      return '';
    case HTTP_STATUS.INTERNAL_SERVER_ERROR:
      return ERROR_MESSAGES.SERVER_ERROR;
    case HTTP_STATUS.SERVICE_UNAVAILABLE:
      return ERROR_MESSAGES.SERVER_ERROR;
    default:
      return ERROR_MESSAGES.UNKNOWN_ERROR;
  }
};

/**
 * GET
 * HTTP GET
 * Axios GET
 * GET
 * 
 * @param url URL
 * @param config 
 * @returns Promise<>
 */
export const get = <T = any>(
  url: string, 
  config?: RequestConfig
): Promise<ApiResponse<T>> => {
  return httpClient.get(url, config).then(response => response.data);
};

