/**
 * 
 * 
 * FormData
 * 
 * 
 * @param url URL
 * @param file 
 * @param onProgress 
 * @param config 
 * @returns Promise<>
 */
export const uploadFile = <T = any>(
  url: string,
  file: File,
  onProgress?: (progress: number) => void,
  config?: RequestConfig
): Promise<ApiResponse<T>> => {
  const formData = new FormData();
  formData.append('file', file);
  
  const uploadConfig: RequestConfig = {
    ...config,
    headers: {
      ...config?.headers,
      'Content-Type': 'multipart/form-data',
    },
    onUploadProgress: (progressEvent) => {
      if (onProgress && progressEvent.total) {
        const progress = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        onProgress(progress);
      }
    },
  };
  
  return httpClient.post(url, formData, uploadConfig).then(response => response.data);
};

/**
 * 
 * 
 * BlobAPI
 * 
 * 
 * @param url URL
 * @param filename 
 * @param config 
 * @returns Promise<void>
 */
export const downloadFile = async (
  url: string,
  filename?: string,
  config?: RequestConfig
): Promise<void> => {
  const downloadConfig: RequestConfig = {
    ...config,
    responseType: 'blob',
  };
  
  const response = await httpClient.get(url, downloadConfig);
  
  // 
  const blob = new Blob([response.data]);
  const downloadUrl = window.URL.createObjectURL(blob);
  
  // 
  const link = document.createElement('a');
  link.href = downloadUrl;
  link.download = filename || 'download';
  document.body.appendChild(link);
  link.click();
  
  // 
  document.body.removeChild(link);
  window.URL.revokeObjectURL(downloadUrl);
};

/**
 * 
 * 
 * 
 * 
 * 
 * @param requestFn 
 * @param maxRetries 
 * @param delay 
 * @returns Promise<>
 */
export const retryRequest = async <T>(
  requestFn: () => Promise<T>,
  maxRetries: number = 3,
  delay: number = 1000
): Promise<T> => {
  let lastError: any;
  
  for (let i = 0; i <= maxRetries; i++) {
    try {
      return await requestFn();
    } catch (error) {
      lastError = error;
      
      // 
      if (i === maxRetries) {
        throw error;
      }
      
      // 
      await new Promise(resolve => setTimeout(resolve, delay * Math.pow(2, i)));
    }
  }
  
  throw lastError;
};

/**
 * 
 * 
 * Promise.all
 * 
 * 
 * @param requests 
 * @returns Promise<>
 */
export const concurrentRequests = async <T>(
  requests: (() => Promise<T>)[]
): Promise<T[]> => {
  return Promise.all(requests.map(request => request()));
};

/**
 * 
 * 
 * AbortController
 * 
 */
export class RequestController {
  private abortController: AbortController;
  
  constructor() {
    this.abortController = new AbortController();
  }
  
  /**
   * 
   * 
   * AbortController API
   * 
   * 
   * @returns 
   */
  getSignal(): AbortSignal {
    return this.abortController.signal;
  }
  
  /**
   * 
   * 
   * AbortController.abort()
   * 
   */
  cancel(): void {
    this.abortController.abort();
  }
  
  /**
   * 
   * 
   * AbortController
   * 
   */
  reset(): void {
    this.abortController = new AbortController();
  }
}

/**
 * HTTP
 * HTTP
 * 
 * API
 */
export default {
  get,
  post,
  put,
  patch,
  delete: del,
  uploadFile,
  downloadFile,
  retryRequest,
  concurrentRequests,
  RequestController,
  httpClient
};