/**
 * Store
 * Zustand
 * ZustandTypeScript
 * TypeScript
 * 
 * @author fenix
 * @date 2025-01-27
 * @version 1.0
 */

import { create } from 'zustand';
import { devtools, persist, subscribeWithSelector } from 'zustand/middleware';
import { immer } from 'zustand/middleware/immer';
import { 
  AuthState, 
  UserInfo, 
  LoginRequest, 
  RegisterRequest, 
  AuthResponse,
  ChangePasswordRequest,
  ForgotPasswordRequest,
  ResetPasswordRequest,
  SessionInfo
} from '../types/auth';
import { 
  STORAGE_KEYS, 
  SESSION_CONFIG, 
  AUTH_EVENTS
} from '../constants/auth';
import {
  login as loginApi,
  register as registerApi,
  logout as logoutApi,
  refreshToken as refreshTokenApi,
  getCurrentUser,
  changePassword as changePasswordApi,
  forgotPassword as forgotPasswordApi,
  resetPassword as resetPasswordApi,
  verifyToken,
  getUserSessions,
  terminateSession,
  terminateAllOtherSessions
} from '../services/authService';
import {
  isTokenExpired,
  parseJwtPayload,
  extractUserFromToken,
} from '../utils/auth';
import {
  setToken,
  getToken,
  removeToken,
  setRefreshToken,
  getRefreshToken,
  removeRefreshToken
} from '../utils/tokenStorage';

/**
 * Store
 * Store
 * TypeScript
 * 
 */
interface AuthStore extends AuthState {
  // 
  login: (loginData: LoginRequest) => Promise<void>;
  register: (registerData: RegisterRequest) => Promise<void>;
  logout: () => Promise<void>;
  
  // 
  refreshToken: () => Promise<void>;
  checkTokenValidity: () => Promise<boolean>;
  
  // 
  fetchUserInfo: () => Promise<void>;
  updateUserInfo: (userInfo: Partial<UserInfo>) => void;
  
  // 
  changePassword: (changePasswordData: ChangePasswordRequest) => Promise<void>;
  forgotPassword: (forgotPasswordData: ForgotPasswordRequest) => Promise<void>;
  resetPassword: (resetPasswordData: ResetPasswordRequest) => Promise<void>;
  
  // 
  fetchSessions: () => Promise<SessionInfo[]>;
  terminateSession: (sessionId: string) => Promise<void>;
  terminateAllOtherSessions: () => Promise<void>;
  
  // 
  setLoading: (loading: boolean) => void;
  setError: (error: string | null) => void;
  clearError: () => void;
  updateLastActivity: () => void;
  
  // 
  initialize: () => Promise<void>;
  reset: () => void;
}

/**
 * 
 * Store
 * 
 * 
 */
const initialState: AuthState = {
  isAuthenticated: false,
  isLoading: false,
  user: null,
  accessToken: null,
  tokenExpiry: null,
  error: null,
  lastActivity: Date.now()
};

/**
 * Store
 * ZustandStore
 * Zustand create
 * 
 */
export const useAuthStore = create<AuthStore>()(
  devtools(
    persist(
      subscribeWithSelector(
        immer<AuthStore>((set, get) => ({
            ...initialState,

            /**
             * 
             * 
             * 
             * 
             */
            login: async (loginData: LoginRequest) => {
              set((state) => {
                state.isLoading = true;
                state.error = null;
              });

              try {
                // API
                const authResponse: AuthResponse = await loginApi(loginData);
                
                // 
                setToken(authResponse.accessToken);
                setRefreshToken(authResponse.refreshToken);
                
                // 
                const tokenExpiry = Date.now() + (authResponse.expiresIn * 1000);
                
                // 
                set((state) => {
                  state.isAuthenticated = true;
                  state.user = authResponse.user;
                  state.accessToken = authResponse.accessToken;
                  state.tokenExpiry = tokenExpiry;
                  state.lastActivity = Date.now();
                  state.isLoading = false;
                  state.error = null;
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.LOGIN_SUCCESS, {
                  detail: { user: authResponse.user }
                }));
                
              } catch (error: any) {
                set((state) => {
                  state.isLoading = false;
                  state.error = error.message || '';
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.LOGIN_FAILED, {
                  detail: { error: error.message }
                }));
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            register: async (registerData: RegisterRequest) => {
              set((state) => {
                state.isLoading = true;
                state.error = null;
              });

              try {
                // API
                const authResponse: AuthResponse = await registerApi(registerData);
                
                // 
                setToken(authResponse.accessToken);
                setRefreshToken(authResponse.refreshToken);
                
                // 
                const tokenExpiry = Date.now() + (authResponse.expiresIn * 1000);
                
                // 
                set((state) => {
                  state.isAuthenticated = true;
                  state.user = authResponse.user;
                  state.accessToken = authResponse.accessToken;
                  state.tokenExpiry = tokenExpiry;
                  state.lastActivity = Date.now();
                  state.isLoading = false;
                  state.error = null;
                });
                
              } catch (error: any) {
                set((state) => {
                  state.isLoading = false;
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            logout: async () => {
              set((state) => {
                state.isLoading = true;
              });

              try {
                // API
                await logoutApi();
              } catch (error) {
                console.error('Logout API failed:', error);
                // API
              } finally {
                // 
                removeToken();
                removeRefreshToken();
                
                // 
                set((state) => {
                  Object.assign(state, initialState);
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.LOGOUT));
                
                // 
                window.location.href = '/login';
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            refreshToken: async () => {
              const refreshToken = getRefreshToken();
              
              if (!refreshToken) {
                throw new Error('No refresh token available');
              }

              try {
                // API
                const authResponse: AuthResponse = await refreshTokenApi(refreshToken);
                
                // 
                setToken(authResponse.accessToken);
                setRefreshToken(authResponse.refreshToken);
                
                // 
                const tokenExpiry = Date.now() + (authResponse.expiresIn * 1000);
                
                // 
                set((state) => {
                  state.accessToken = authResponse.accessToken;
                  state.tokenExpiry = tokenExpiry;
                  state.lastActivity = Date.now();
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.TOKEN_REFRESHED));
                
              } catch (error: any) {
                // 
                removeToken();
                removeRefreshToken();
                
                set((state) => {
                  Object.assign(state, initialState);
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.TOKEN_EXPIRED));
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            checkTokenValidity: async (): Promise<boolean> => {
              const token = getToken();
              
              if (!token) {
                return false;
              }
              
              // 
              const state = get();
              if (isTokenExpired(state.tokenExpiry)) {
                try {
                  // 
                  await get().refreshToken();
                  return true;
                } catch (error) {
                  return false;
                }
              }
              
              // 
              try {
                const isValid = await verifyToken();
                
                if (!isValid) {
                  // 
                  get().reset();
                }
                
                return isValid;
              } catch (error) {
                return false;
              }
            },

            /**
             * 
             * 
             * API
             * 
             */
            fetchUserInfo: async () => {
              try {
                const userInfo = await getCurrentUser();
                
                set((state) => {
                  state.user = userInfo;
                });
                
                // 
                window.dispatchEvent(new CustomEvent(AUTH_EVENTS.USER_UPDATED, {
                  detail: { user: userInfo }
                }));
                
              } catch (error: any) {
                set((state) => {
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            updateUserInfo: (userInfo: Partial<UserInfo>) => {
              set((state) => {
                if (state.user) {
                  Object.assign(state.user, userInfo);
                }
              });
            },

            /**
             * 
             * 
             * API
             * 
             */
            changePassword: async (changePasswordData: ChangePasswordRequest) => {
              set((state) => {
                state.isLoading = true;
                state.error = null;
              });

              try {
                await changePasswordApi(changePasswordData);
                
                set((state) => {
                  state.isLoading = false;
                });
                
              } catch (error: any) {
                set((state) => {
                  state.isLoading = false;
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * API
             * 
             */
            forgotPassword: async (forgotPasswordData: ForgotPasswordRequest) => {
              set((state) => {
                state.isLoading = true;
                state.error = null;
              });

              try {
                await forgotPasswordApi(forgotPasswordData);
                
                set((state) => {
                  state.isLoading = false;
                });
                
              } catch (error: any) {
                set((state) => {
                  state.isLoading = false;
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * API
             * 
             */
            resetPassword: async (resetPasswordData: ResetPasswordRequest) => {
              set((state) => {
                state.isLoading = true;
                state.error = null;
              });

              try {
                await resetPasswordApi(resetPasswordData);
                
                set((state) => {
                  state.isLoading = false;
                });
                
              } catch (error: any) {
                set((state) => {
                  state.isLoading = false;
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * API
             * 
             */
            fetchSessions: async (): Promise<SessionInfo[]> => {
              try {
                return await getUserSessions();
              } catch (error: any) {
                set((state) => {
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * API
             * 
             */
            terminateSession: async (sessionId: string) => {
              try {
                await terminateSession(sessionId);
              } catch (error: any) {
                set((state) => {
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            terminateAllOtherSessions: async () => {
              try {
                await terminateAllOtherSessions();
              } catch (error: any) {
                set((state) => {
                  state.error = error.message || '';
                });
                
                throw error;
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            setLoading: (loading: boolean) => {
              set((state) => {
                state.isLoading = loading;
              });
            },

            /**
             * 
             * 
             * 
             * 
             */
            setError: (error: string | null) => {
              set((state) => {
                state.error = error;
              });
            },

            /**
             * 
             * 
             * 
             * 
             */
            clearError: () => {
              set((state) => {
                state.error = null;
              });
            },

            /**
             * 
             * 
             * 
             * 
             */
            updateLastActivity: () => {
              set((state) => {
                state.lastActivity = Date.now();
              });
            },

            /**
             * 
             * 
             * 
             * 
             */
            initialize: async () => {
              try {
                const token = getToken();
                
                if (!token) {
                  set((state) => {
                    state.isLoading = false;
                    state.isAuthenticated = false;
                  });
                  return;
                }
                
                // 
                const storedUser = localStorage.getItem('user');
                let userFromStorage = null;
                if (storedUser) {
                  try {
                    userFromStorage = JSON.parse(storedUser);
                  } catch (e) {
                    // JSON
                  }
                }
                
                // token
                let userFromToken = null;
                try {
                  const tokenPayload = parseJwtPayload(token);
                  if (tokenPayload) {
                    userFromToken = extractUserFromToken(token); // token
                  }
                } catch (e) {
                  // token
                }
                
                // token
                const currentUser = get().user;
                const initialUser = currentUser || userFromStorage || userFromToken;

                set((state) => {
                  state.isAuthenticated = true;
                  state.accessToken = token;
                  if (!state.user && initialUser) {
                    state.user = initialUser;
                  }
                  state.lastActivity = Date.now();
                  state.isLoading = true;
                  state.error = null;
                });
                
                // token
                try {
                  const userInfo = await getCurrentUser();
                  
                  // 
                  const existingUser = get().user as any;
                  const sanitizedServer = Object.fromEntries(Object.entries(userInfo as any).filter(([_, v]) => v !== undefined && v !== null));
                  const mergedUser = existingUser ? { ...existingUser, ...sanitizedServer } : userInfo;

                  set((state) => {
                    state.user = mergedUser as any;
                    state.isLoading = false;
                    state.error = null;
                  });
                  
                  localStorage.setItem('user', JSON.stringify(mergedUser));
                  
                } catch (userInfoError: any) {
                  
                  // 
                  if (userInfoError.status === 401 || userInfoError.message?.includes('')) {
                    get().reset();
                  } else {
                    set((state) => {
                      state.isLoading = false;
                      state.error = `${userInfoError.message || ''}`;
                    });
                    window.dispatchEvent(new CustomEvent('auth:fallback:mode', {
                      detail: { error: userInfoError.message }
                    }));
                  }
                }
                
              } catch (error) {
                get().reset();
              }
            },

            /**
             * 
             * 
             * 
             * 
             */
            reset: () => {
              removeToken();
              removeRefreshToken();
              
              set((state) => {
                Object.assign(state, initialState);
              });
            }
          }))
        ),
        {
          name: STORAGE_KEYS.AUTH_STATE,
          partialize: (state) => ({
            isAuthenticated: state.isAuthenticated,
            user: state.user,
            lastActivity: state.lastActivity,
            tokenExpiry: state.tokenExpiry
          })
        }
      ),
      {
        name: 'auth-store'
      }
    )
  );

/**
 * Hook
 * 
 * 
 * 
 */
export const useSessionTimeout = () => {
  const { isAuthenticated, lastActivity, logout, updateLastActivity } = useAuthStore();
  
  React.useEffect(() => {
    if (!isAuthenticated) {
      return;
    }
    
    const checkSessionTimeout = () => {
      const now = Date.now();
      const timeSinceLastActivity = now - lastActivity;
      
      if (timeSinceLastActivity > SESSION_CONFIG.SESSION_TIMEOUT) {
        // 
        logout();
        
        // 
        window.dispatchEvent(new CustomEvent(AUTH_EVENTS.SESSION_TIMEOUT));
      }
    };
    
    // 
    const interval = setInterval(checkSessionTimeout, SESSION_CONFIG.ACTIVITY_CHECK_INTERVAL);
    
    // 
    const handleUserActivity = () => {
      updateLastActivity();
    };
    
    // 
    const events = ['mousedown', 'mousemove', 'keypress', 'scroll', 'touchstart'];
    events.forEach(event => {
      document.addEventListener(event, handleUserActivity, true);
    });
    
    return () => {
      clearInterval(interval);
      events.forEach(event => {
        document.removeEventListener(event, handleUserActivity, true);
      });
    };
  }, [isAuthenticated, lastActivity, logout, updateLastActivity]);
};

/**
 * Hook
 * 
 * 
 * 
 */
export const useTokenRefresh = () => {
  const { isAuthenticated, tokenExpiry, refreshToken } = useAuthStore();
  
  React.useEffect(() => {
    if (!isAuthenticated || !tokenExpiry) {
      return;
    }
    
    const checkTokenExpiry = async () => {
      const now = Date.now();
      const timeUntilExpiry = tokenExpiry - now;
      
      // 
      if (timeUntilExpiry <= 60000 && timeUntilExpiry > 0) { // 
        try {
          await refreshToken();
        } catch (error) {
          // 
        }
      }
    };
    
    // 
    const interval = setInterval(checkTokenExpiry, 60000); // 
    
    return () => {
      clearInterval(interval);
    };
  }, [isAuthenticated, tokenExpiry, refreshToken]);
};

/**
 * Hook
 * 
 * Effect Hook
 * 
 */
export const useAuthInitialization = () => {
  const initialize = useAuthStore(state => state.initialize);
  
  React.useEffect(() => {
    initialize();
  }, [initialize]);
};

// ReactHook
import React from 'react';

export default useAuthStore;